
h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

*{
    box-sizing: border-box;
}

body{
    background: black;
    color: $white;
    font-family: 'Russia';
    line-height: 1;
}

a{
    outline: none;
}
img{
    max-width: 100%;
    width: 100%;
}

.bg_black{
    background: $black url('../img/source/black_bg_mob.jpg') repeat;
    @include lg-block(){
        background: $black url('../img/source/black_bg.jpg') repeat;
    }
}

.section{
    padding-top: 40px;
    
    &__title{
        font-family: 'Rubik';
        font-weight: 300;
        font-size: 24px;
        text-align: center;
        &_wrapper{
            position: relative;
        }

    }
    &_home{
        padding-top: 0;
    }
    &_advantages{
        padding-bottom:  rem(55);
        background: url('../img/source/black_bg_mob.jpg') repeat;
        @include lg-block(){
            background: url('../img/source/black_bg.jpg') repeat;
            padding-bottom: rem(40);
        }
    }
    &_gallery{
        position: relative;
        z-index: 2;
    }
    &_hall{
        padding-bottom: rem(40);
    }
}


/*==============BUTTON=================================*/
.btn{
    border: none;
    color: $black;
    font-size: $small_text;
    display: block;
 
    max-width: 100%;
    outline: none;
 
  
    &:hover{
        cursor: pointer;
    }

    &_hall{
        display: none;
        @include lg-block(){
            display: block;
            margin-top: rem(40);
        }
    }

}
.btn_callToAction{
   

    margin-left: auto;
    margin-right: auto;
    width: rem(196);
    padding: 15px 30px;
    position: relative;
    z-index: 2;

    @include md-block{
        font-size: rem(24);
        width: rem(300);
        padding: 20px 40px;
        
    };

    border-radius: 40px;

    background: #bb881a;
    background: -moz-linear-gradient(top, #bb881a 0%, #dbcc5c 30%, #fef4a4 50%, #dbcc5c 69%, #bb881a 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, #bb881a), color-stop(30%, #dbcc5c), color-stop(50%, #fef4a4), color-stop(69%, #dbcc5c), color-stop(100%, #bb881a));
    background: -webkit-linear-gradient(top, #bb881a 0%, #dbcc5c 30%, #fef4a4 50%, #dbcc5c 69%, #bb881a 100%);
    background: -o-linear-gradient(top, #bb881a 0%, #dbcc5c 30%, #fef4a4 50%, #dbcc5c 69%, #bb881a 100%);
    background: -ms-linear-gradient(top, #bb881a 0%, #dbcc5c 30%, #fef4a4 50%, #dbcc5c 69%, #bb881a 100%);
    background: linear-gradient(to bottom, #bb881a 0%, #dbcc5c 30%, #fef4a4 50%, #dbcc5c 69%, #bb881a 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bb881a', endColorstr='#bb881a', GradientType=0 );

    box-shadow: 0 0 10px 5px $accent_05;
    transition: all ease 0.3s;


    &:hover{
        box-shadow: 0 0 10px 5px $accent_00;
        transition: all ease 0.3s;
    }

  
}


.arrow{
    width: rem(25);
    height: rem(30);

    @include md-block(){
        width: rem(30);
        height: rem(35);
    }


    @include lg-block(){
        width: rem(50);
        height: rem(80);
    }
}


/*==============BUTTON END=================================*/



/*=====SECTION_HOME================*/

.home{
    position: relative;
    z-index: 2;
    &__title{
        font-weight: bold;
        font-size: 24px;

        @include md(font-size, rem(42));

        @include lg(font-size, rem(72));

        margin-bottom: 40px;
        text-transform: uppercase;
    }
    &__subtitle{
        color: $accent;
        font-weight: normal;
        text-transform: uppercase;
        font-size: $small_text;
        margin-bottom: rem(40);

        @include md(margin-bottom, rem(100));

        @include lg-block{
            margin-bottom: rem(130);
            font-size: rem(30);
        
        }

    }
}

.slider_home{
    &_wrapper{
        position: absolute;
        top: 0;
        max-width: 100%;
        z-index: 1;
        left: 0;
        .lSAction{  
            position: relative;
            @include container();
            @include sm(max-width, rem(510));
            @include md(max-width, rem(690));
            @include lg(max-width, rem(930));
            @include xl(max-width, rem(1110));

            >  .lSNext{
                top: auto;
                bottom: 0px;
                right: 0;
                @include lg-block{
                    bottom: rem(50);
                    right: -20px;

                }
       
               

            }
        
            > .lSPrev{
                top: auto;
                left: 0;
                bottom: 0px;
                @include lg-block{
                    bottom: rem(50);
                    left: -10px;

                }
            }
        }

    }
    &__slide{
        width: 100vw;
        max-width: 100%;

        height: rem(400);

        @include md(height,600px);

        @include lg(height,100vh);

        max-height: 100%;

        background: center no-repeat;
        background-size: cover;
        &:nth-child(1){
            background-image: url('../img/source/home_slider/slide_1.jpg');
        }
        &:nth-child(2){
            background-image: url('../img/source/home_slider/slide_2.jpg');
        }
        &:nth-child(3){
            background-image: url('../img/source/home_slider/slide_3.jpg');
        }
        &:nth-child(4){
            background-image: url('../img/source/home_slider/slide_4.jpg');
        }

    }
}


/*=====SECTION_HOME END================*/


/*=====SECTION_ADVANTAGES ================*/


.advantages{
    @include  row-flex();
    margin-top: rem(50);
    @include lg-block(){
       margin: rem(50) 0 0 0;
       padding: 0; 
    }
    &__item{
        list-style: none;
        @include col();
        @include size(6);
        @include size-lg(4);

        @include lg-block(){
             margin: 0;
            padding: 0; 
        }
      
        position: relative;
    }
}

.advantage{
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;

 

    align-items: center;
    margin-bottom: rem(13);

    @include lg(margin-bottom, rem(0));
  

    &__title{
        font-family: 'Rubik';
       
        font-weight: 300;
        width: 130px;
        height: 35px;
        text-align: center;
        vertical-align: middle;
        padding-top: 12px;
        font-size: 12px;

        @include sm-block(){
            max-width:  100%;
            width: 199px;
            height: 53px;
            padding-top: 18px; 
            font-size: 18px;
        }
       
       @include lg-block(){
            position: absolute;
            bottom: 15px;
        }
       

        display: inline-block;

        background:  url('../img/source/advantages/button_border.png')  rgba(0, 0, 0, .5) no-repeat;
        background-size: contain;
        
    }

    &__img{
        width: 100%;
        margin-bottom: rem(10);
        @include lg(margin-bottom, rem(0));
    }
}


/*=====SECTION_ADVANTAGES end ================*/


/*=====SECTION_GALLERY  ================*/

.imageGallery{
    &__item{
        > img {
            width: 100%;
            &:hover{
                cursor: url('../img/source/loop.png'), pointer;
                transition: all ease 0.3s;
            }
        }
    }
    &_wrapper{
        .lSPager.lSGallery{
            margin-left: auto;
            margin-right: auto;
           li{
                opacity: 0.5;
       
                &.active{
                    opacity: 1;
                  
                }
           }
    
        }
    
    }
   
}
.galleryControll{
    position: absolute;
    top: 50%;
    z-index: 2;
    width: 100%;

    .container{
        display: flex;
        justify-content: space-between;
    }

    &__arrow{
        opacity: .75;
        transition: all ease .3s;
        &:hover{
            opacity: 1;
            transition: all ease .3s;
            cursor: pointer;
        }
    }

}
.container_thumbs{
    display: flex;
    justify-content: center;
}


/*============SECTION_HALL=========================================*/


#hall {
    max-width: 100%;
    height: auto;
    
    .red{
        fill: #630100;
        &:hover{
            cursor: pointer;
            fill: #820a09;
        }
    }
}


.tooltip{
    background: $white;
    border-radius: 15px;
    position: relative;
    width: 270px;
    height: 210px;
    max-width: 100%;
    visibility: hidden;
    opacity: 0;
    transition : all ease 0.3s;
    margin-left: auto;
    z-index: 2;

    margin-right: auto;

    margin-top: rem(40);

    @include lg-block(){
       
    
        position: absolute;
    }

    &_active{
        // @include lg-block(){
            opacity: 1;
            transition : all ease 0.3s;
            visibility: visible;
        // }
    }

    &:before{
        content: '';
       
        border: 15px solid transparent;
        position: absolute;
        border-bottom: 15px solid $white;

        top: -30px;
        left: 50%;

        @include lg-block(){
            border: 15px solid transparent;
            border-top: 15px solid $white;
            bottom: -30px;
            left: 30px;
            top: auto;
        }
    }

    &__title{
        color: $black;
    }

    &__btn{
        background: #931717;
        padding: 11px 20px;
        border-radius: 10px;
        color: $white;
        font-weight: 100;
    }

    &__img{
        border-radius: 13px 13px 0 0;
    }

}

.textTip{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    font-family: 'Rubik';
    font-size: $small_text;
    font-weight: 300;
    @include lg(font-size, 20);
}

