.header{
   @include row-flex();
   align-items: center;
   padding-top: rem(40);
   position: relative;
   z-index: 2;
   margin-bottom: rem(25);
   @include md(margin-bottom, rem(100));
   @include lg(margin-bottom, rem(300));

   &_wrapper{
       position: static;
    //    top: 0;
    //    left: 0;
    //    right: 0;
    //    z-index: 5;
   }



}


.logo{
    @include col();
    @include size(12);
    @include size-lg(6);
    @include size-xl(7);

    order:  1;
    
    margin-top: rem(27);
  
    @include lg-block(){
        order: 0;
        margin-top: 0;
        margin-top: rem(57);

    }

  
    &__img{ 
        width: rem(180);
        height: rem(70);
        
        @include lg-block(){
            width: rem(285);
            height: rem(115);
            
        }
    }
    &__name, &__description{
        height: 0;
        position: absolute;
        display: none;
    }

}



.contact{
    @include col();
    @include size(8);
    @include size-lg(5);
    @include size-xl(4);

    height: rem(53);
    margin-top: rem(5);



    @include lg(text-align, right); 

    &__phone{       
        font-size: rem(24);       
        font-family: 'Russia';
        color: $white;
        text-decoration: none;
        transition: all ease 0.3s;

        @include lg(font-size, rem(30)); 

        &:hover{
            text-decoration: underline;
            transition: all ease 0.3s;
        }
    }
    &__adress{
        font-size: $small_text;
        margin: 0;
        margin-top: rem(5);
    }
}


.burger{
    width: rem(53);
    max-width: 100%;
    &_wrapper{
        @include col();
        @include size(4);
        @include size-lg(1);
        height: rem(53);
        display: flex;
        justify-content: flex-end;
        margin-top: rem(5);
    }

    &:hover{
        cursor: pointer;
    }
   
}